.head-of-list > div {
  font-weight: 700;
  padding: 5%;
}

.position-item {
  display: grid;
  grid-template-columns: 3rem repeat(4, 1fr) 3rem;
  padding: 5px 50px 5px 50px;
  border-bottom: 1px solid black;
  justify-content: center;
  gap: 1rem;
  position: static;
}

.position-item > div {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
}

