.main-view {
  height: 90vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.selected-event {
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #ddd;
  background-color: #f7f7f7;
}

.page-container {
  background-color: white;
  border: 3px solid white;
  border-radius: 0.5rem;
  width: 80%;
  height: 95%;
  overflow: auto;
}
