.modalBackground{
    width: 100vw;
    height: 100vh;
    background-color: rgb(217, 229, 247);
    position: fixed;
    justify-content: center;
    align-items: center;
    display: flex;
    
}

.modalContainer{
    width: 60vw;
    height: 80vh;
    border-radius: 12px;
    background-color: white;
    box-shadow: rgba(0,0,0, 0.35) 0px 5px 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 25px;
    text-align: center;
}

.titleCloseBtn{
    margin-left: auto;
}

.titleCloseBtn button{
    background-color: transparent;
    border: none;
    font-size: 20px;
}