.container {
    display: flex;
    overflow-x: auto;
}

table {
    border-collapse: collapse;
    width: auto;
    white-space: nowrap;
}

td {
    padding: 8px 12px;
    border: 1px solid #ddd;
    text-align: center;
}

tr:nth-child(odd) {
    background-color: #f2f2f2;
}

tr:nth-child(even) {
    background-color: white;
    text-align: center;
}