.position-description {
  grid-column: 1 / 2;
  grid-row: 2 / -1;
  display: flex;
  gap: 1rem;
  flex-flow: column;
  padding: 1%;
}
.position-description > div {
  text-align: left;
}
.position-card-position-name {
  font-size: x-large;
  font-weight: bold;
  display: flex;
  gap: 1%;
}
.position-card-position-name > div {
  display: flex;
  align-items: center;
}
.position-card-position-name > div > a {
  color: #0a66c2;
  display: flex;
  align-items: center;
}
.position-card-company-name {
  font-size: larger;
}
.position-card-linkedin > a {
  color: #0a66c2;
}