.logo-container {
  width: 10vh;
  height: 10vh;
  border-radius: 50%;
  overflow: hidden;
}

.logo-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.nav-button {
  height: 100%;
  width: 10%;
  display: flex;
  align-items: center;
  padding: 0 3vh;
  border-radius: 0.5rem 0.5rem 0 0 !important;
}

.logo-link {
  text-decoration: none;
  display: flex;
  align-items: center;
  color: inherit;
}

.app-title {
  margin-left: 8px;
}
